import {
    Tabbar,
    TabbarItem,
    Toast,
    Search,
    NavBar,
    Swipe, 
    SwipeItem,
    Dialog,
    Icon,
    Row,
    Col,
    Cell,
    Uploader, 
    Button,
    Grid,
    GridItem,
    Calendar,
    Popup,
    Tab,
    Tabs,
    List,
    Form,
    Field,
    Empty,
    SwipeCell,
    Card
  
} from 'vant'

const vants = [
    Tabbar,
    TabbarItem,
    Toast,
    Search,
    NavBar,
    Swipe, 
    SwipeItem,
    Dialog,
    Icon,
    Row,
    Col,
    Cell,
    Uploader,
    Button,
    Grid,
    GridItem,
    Calendar,
    Popup,
    Tab, 
    Tabs,
    List,
    Form,
    Field,
    Empty,
    SwipeCell,
    Card
    
  
]
export default {
    /**
     * Vue.use 方法会自动调用install函数
     * Install Vant Component plugin
     * @param Vue
     */
    install(Vue) {
        vants.forEach(Component => {
            Vue.use(Component)
        })
    }

}