import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
       
        serchKey: ''
    },
    mutations: {
        setSerchKey(state, key) { 
            state.serchKey = key; 
        }
    },
    actions: {
        setSerchKey({ commit }, key) {
            commit('setSerchKey', key)
        },
    }
})