export const routes  = [
  {
    // 登录
      path:'/login',
      name:'Login',
      component:() => import('../views/Login/Login.vue')
  },
    {
      path:'/',
      name:'Main',
      component:() => import( '../views/Main.vue'),
      children:[
        {
          path: '/',
          name: 'Home',
          component:() => import( '../views/mainviews/Home.vue')
        },
        {
          path: 'classify/:id',
          name: 'Classify',
          component: () => import('../views/mainviews/Classify.vue')
        },
        {
          path: 'bookrack',
          name: 'Bookrack',
          component: () => import('../views/mainviews/Bookrack.vue')
        },
        {
          path: 'my',
          name: 'My',
          component: () => import('../views/mainviews/My.vue')
        },
      ]
    },
    {
      path:'/search',
      name:'Search',
      component: () => import('../views/Search.vue')
    },
    {
      path:'/list',
      name:'List',
      component:() => import('../views/List.vue')
    },
    {
      path: '/about',
      name: 'About',
      component: () => import ('../views/About/About.vue')
    },
    {
      path:'/registration',
      name:'Registration',
      component: () => import('../components/Registration.vue')
    },
    {
      path:'/detail',
      name:'Detail',
      component:() => import('../components/Detail.vue')
    },
    {
      path:'/category',
      name:'Category',
      component:() => import('../components/Category.vue')
    },
    {
      path:'/hot',
      name:'Hot',
      component:() => import('../components/Hot.vue')
    },
    {
      path:'/account',
      name:'Account',
      component:() => import('../components/Account.vue')
    },
    {
      path:'/secure',
      name:'Secure',
      component:() => import('../components/Secure.vue')
    },
    {
      path:'/message',
      name:'Message',
      component:() => import('../components/Message.vue')
    }
  ]

  